* {
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "Saol Display Regular";
  src: url("http://db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.eot");
  src: url("http://db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.woff") format("woff"), url("//db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/4c4c08af466e9ad071b6d69cf44093df.svg#Saol Display Regular") format("svg");
}

body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  /* background-color: #0a192f */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Contact {
    opacity: 0;
}

.form-animation {
    opacity: 1;
    -webkit-animation: right-anim 1s;
            animation: right-anim 1s;
}
@-webkit-keyframes loading-anim {
    0% {

        -webkit-transform: translateY(0);

                transform: translateY(0);
    }

    100% {

        -webkit-transform: translateY(-130%);

                transform: translateY(-130%);
    }
}

@keyframes loading-anim {
    0% {

        -webkit-transform: translateY(0);

                transform: translateY(0);
    }

    100% {

        -webkit-transform: translateY(-130%);

                transform: translateY(-130%);
    }
}

@-webkit-keyframes loading-anim-opac {
    0% {

        opacity: 1;
    }

    100% {

        opacity: 0;
    }
}

@keyframes loading-anim-opac {
    0% {

        opacity: 1;
    }

    100% {

        opacity: 0;
    }
}

.Not-Found {
    width: 100vw;
    height: 100vh;
    background-color: #ccd6f6;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    -webkit-animation: loading-anim 3s 3s;
            animation: loading-anim 3s 3s;
}

.loading-title {
    /* background-color: rgb(151, 96, 96); */
    /* flex-basis: 80%; */
    font-family: 'Playfair Display', serif;
    font-size: 5rem;
    margin: 1rem 2rem;
    font-weight: 100;
    /* letter-spacing: 0.1rem; */
    /* font-style: italic; */

}

.loading-title::first-letter {
    font-size: 8rem;
}

.loading-div {

    display: flex;
    /* background-color: aqua; */
    /* flex-basis: 20%; */
    align-self: flex-end;
    margin-right: 3rem;
    font-size: 4rem;
    font-family: Roboto, sans-serif;
    margin-bottom: 2rem;

}

.percent {

    align-self: flex-end;
}

@media only screen and (max-width: 360px) {
    .loading-title::first-letter {
        font-size: 6rem;
    }

    .loading-title {
        font-size: 3rem;
    }

    .loading-div {
        font-size: 2rem;
    }
}
@-webkit-keyframes anim {
    0% {
        -webkit-transform: translateY(40%);
                transform: translateY(40%);
        opacity: 0
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1
    }
}

@keyframes anim {
    0% {
        -webkit-transform: translateY(40%);
                transform: translateY(40%);
        opacity: 0
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1
    }
}

.root {
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
    opacity: 0;
}

.animation {
    -webkit-animation: anim 1s;
            animation: anim 1s;
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}
@-webkit-keyframes left-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40%);
                transform: translateX(-40%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@keyframes left-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-40%);
                transform: translateX(-40%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@-webkit-keyframes right-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateX(40%);
                transform: translateX(40%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@keyframes right-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateX(40%);
                transform: translateX(40%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
}

@-webkit-keyframes bottom-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateY(60%);
                transform: translateY(60%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

@keyframes bottom-anim {
    0% {
        opacity: 0;
        -webkit-transform: translateY(60%);
                transform: translateY(60%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
}

.Services {
    opacity: 0;
    width: 100%;
    height: 100%;
}

.Services-Animate {
    opacity: 1;
}

.Services-left-animate {
    -webkit-animation: left-anim 2s;
            animation: left-anim 2s;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}

.Services-right-animate {
    -webkit-animation: right-anim 2s;
            animation: right-anim 2s;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}

.Services-bottom-animate {
    -webkit-animation: bottom-anim 2s;
            animation: bottom-anim 2s;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}
@-webkit-keyframes anim1 {
    0% {
        -webkit-transform: translateY(200%);
                transform: translateY(200%);
        opacity: 0;
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1;
    }
}

@keyframes anim1 {
    0% {
        -webkit-transform: translateY(200%);
                transform: translateY(200%);
        opacity: 0;
    }

    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
        opacity: 1;
    }
}

.heading {
    -webkit-transform: translateY(200%);
            transform: translateY(200%);
    opacity: 0;
}

.animation1 {
    -webkit-animation: anim1 1s;
            animation: anim1 1s;
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);

}
.App {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}
